import React from 'react';
import ReactDOM from 'react-dom';
import './Style.css';
import Picture from './Picture 1.png';
import SearchBar from './SearchBar';

class App extends React.Component {
  state ={clicks : 0};
  
  IncrementNum =() =>{
  this.setState({clicks : this.state.clicks + 1});
  }

 render () {
   return (
     <div>
       <h2 className ="pageheader">Welcome to Minah Lim</h2>
    
       <div class="ui card">
          <div class="image">
           <img src={Picture} alt="profile pic"></img>
          </div>
          <div class="content">
            <span class="header"> Minah Lim </span>
            <div class="meta">
           <span class="date">07 Sep 1985</span>
          </div>
          <div class="description">Legal entity accountant at apple</div>
         </div>
         <div class="extra content">
           <span>
             <i class="user icon" onClick={this.IncrementNum}></i>{this.state.clicks} friends</span>
        </div>
       </div>
       <SearchBar></SearchBar>
     </div>
    );
   }
}


ReactDOM.render(<App />, document.querySelector('#root'));
